import Vue from 'vue'
import Router from 'vue-router'
const main=r=>require.ensure([],()=>r(require('../page/main.vue')),'main');
const yaowen=r=>require.ensure([],()=>r(require('../page/yaowen.vue')),'yaowen');
const shangjia=r=>require.ensure([],()=>r(require('../page/shangjia.vue')),'shangjia');
const wangshang=r=>require.ensure([],()=>r(require('../page/wangshang.vue')),'wangshang');
const zhanshi=r=>require.ensure([],()=>r(require('../page/zhanshi.vue')),'zhanshi');
Vue.use(Router)
const routes=[{
  path:'/',
  component:main
},{
  path:'/yaowen',
  component:yaowen
},{
  path:"/shangjia",
  component:shangjia
},
{
  path:'/wangshang',
  component:wangshang
},{
  path:'/zhanshi',
  component:zhanshi
}
]
export default new Router({
  routes
})



